*{
    box-sizing: border-box;
    margin: 0;
    font-family: 'Inter', sans-serif;
}
.navbar{
    width: 100%;
    height: 55px;
    margin-bottom: 50px;
    display: flex;
    background-color: #F55A5A;
    position: sticky;
    flex-direction: row;
}

.image {
   width: 100%;
   border-radius: 20px;
   
    
}
.navbar{
    margin-bottom: 100px;
}
.card{
    display: flex;
    width: 100%;
    height: 500px;
   
    
   
}
.divimg {
    
    width: 30%;
    margin: 0 auto;
    
   
}
.card:nth-child(2) .image{
    width: 100%;
}
.divtext{
    width: 30%;
    margin: 2% auto;
    text-align: left;
   margin-left: -10%;
    
}
.location-google{
    height: 30px;
    display: flex;
    margin-bottom: 20%;
}
.location-google > h3{
    margin-top:auto;
}
.location-google > a{
    margin-top: auto;
    margin-left: 20%;
    color: grey;
}
@media screen and (max-width:1000px ){
.divtext{
    font-size: x-small;
    margin-left: 0 ;
}
.divimg{
    width: 50%;
    
}
.location-google{
    margin-bottom: 10%;
}

}

@media screen and(max-width:700px){

.divimg{
    width: 1%;
}

}
.travel{
    font-weight: 700;
    margin: auto auto auto 10px;;
    color: white;
   
    

}
.plane{
    margin: auto 10px auto auto;
    color: white;
    
}
.location-red{
    color: red;
}
.title{
    padding-bottom: 5%;
}